import React, { useState, useEffect } from 'react';
import { CheckCircleIcon, ExclamationTriangleIcon, ClockIcon } from '@heroicons/react/24/outline';

const ScrapingProgress = ({ jobId, onComplete, onError }) => {
  const [progress, setProgress] = useState({
    stage: 'initializing',
    progress: 0,
    total: 1,
    percentage: 0,
    message: 'Starting enhanced scraping...'
  });
  const [status, setStatus] = useState('running'); // running, completed, error

  useEffect(() => {
    if (!jobId) return;

    // Simulate progress updates (in real implementation, this would come from WebSocket or polling)
    const progressStages = [
      { stage: 'initializing', message: 'Initializing enhanced scraping service...', progress: 0, total: 1 },
      { stage: 'scraping', message: 'Searching high-quality business sources...', progress: 1, total: 5 },
      { stage: 'scraping', message: 'Scraping Google News...', progress: 2, total: 5 },
      { stage: 'scraping', message: 'Scraping Bing News...', progress: 3, total: 5 },
      { stage: 'scraping', message: 'Scraping business news sites...', progress: 4, total: 5 },
      { stage: 'scraping', message: 'Scraping construction news...', progress: 5, total: 5 },
      { stage: 'enriching', message: 'Enriching results with full content...', progress: 0, total: 10 },
      { stage: 'extracting', message: 'Extracting lead data with AI...', progress: 0, total: 10 },
      { stage: 'saving', message: 'Saving leads to database...', progress: 0, total: 10 },
      { stage: 'completed', message: 'Scraping completed successfully!', progress: 10, total: 10 }
    ];

    let currentStage = 0;
    const interval = setInterval(() => {
      if (currentStage < progressStages.length) {
        const stage = progressStages[currentStage];
        setProgress({
          ...stage,
          percentage: Math.round((stage.progress / stage.total) * 100)
        });
        currentStage++;
      } else {
        clearInterval(interval);
        setStatus('completed');
        if (onComplete) onComplete();
      }
    }, 2000);

    return () => clearInterval(interval);
  }, [jobId, onComplete]);

  const getStageIcon = () => {
    switch (progress.stage) {
      case 'initializing':
        return <ClockIcon className="w-6 h-6 text-blue-500" />;
      case 'scraping':
        return <div className="w-6 h-6 border-2 border-blue-500 border-t-transparent rounded-full animate-spin" />;
      case 'enriching':
        return <div className="w-6 h-6 border-2 border-green-500 border-t-transparent rounded-full animate-spin" />;
      case 'extracting':
        return <div className="w-6 h-6 border-2 border-purple-500 border-t-transparent rounded-full animate-spin" />;
      case 'saving':
        return <div className="w-6 h-6 border-2 border-orange-500 border-t-transparent rounded-full animate-spin" />;
      case 'completed':
        return <CheckCircleIcon className="w-6 h-6 text-green-500" />;
      default:
        return <ClockIcon className="w-6 h-6 text-gray-500" />;
    }
  };

  const getStageColor = () => {
    switch (progress.stage) {
      case 'initializing':
        return 'bg-blue-500';
      case 'scraping':
        return 'bg-blue-500';
      case 'enriching':
        return 'bg-green-500';
      case 'extracting':
        return 'bg-purple-500';
      case 'saving':
        return 'bg-orange-500';
      case 'completed':
        return 'bg-green-500';
      default:
        return 'bg-gray-500';
    }
  };

  const getStageDescription = () => {
    switch (progress.stage) {
      case 'initializing':
        return 'Setting up enhanced scraping service...';
      case 'scraping':
        return 'Searching multiple high-quality business news sources';
      case 'enriching':
        return 'Extracting full article content from URLs';
      case 'extracting':
        return 'Using AI to extract business lead information';
      case 'saving':
        return 'Saving leads with populated data fields';
      case 'completed':
        return 'High-quality leads successfully saved!';
      default:
        return 'Processing...';
    }
  };

  if (status === 'completed') {
    return (
      <div className="bg-green-50 border border-green-200 rounded-lg p-6">
        <div className="flex items-center space-x-3">
          <CheckCircleIcon className="w-8 h-8 text-green-500" />
          <div>
            <h3 className="text-lg font-semibold text-green-800">Scraping Completed!</h3>
            <p className="text-green-600">Enhanced scraping service has successfully found and saved high-quality leads.</p>
          </div>
        </div>
      </div>
    );
  }

  if (status === 'error') {
    return (
      <div className="bg-red-50 border border-red-200 rounded-lg p-6">
        <div className="flex items-center space-x-3">
          <ExclamationTriangleIcon className="w-8 h-8 text-red-500" />
          <div>
            <h3 className="text-lg font-semibold text-red-800">Scraping Error</h3>
            <p className="text-red-600">An error occurred during scraping. Please try again.</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="bg-white border border-gray-200 rounded-lg p-6 shadow-sm">
      <div className="flex items-center space-x-3 mb-4">
        {getStageIcon()}
        <div>
          <h3 className="text-lg font-semibold text-gray-900">Enhanced Scraping in Progress</h3>
          <p className="text-sm text-gray-600">{getStageDescription()}</p>
        </div>
      </div>

      {/* Progress Bar */}
      <div className="mb-4">
        <div className="flex justify-between text-sm text-gray-600 mb-2">
          <span>{progress.stage.charAt(0).toUpperCase() + progress.stage.slice(1)}</span>
          <span>{progress.percentage}%</span>
        </div>
        <div className="w-full bg-gray-200 rounded-full h-3">
          <div 
            className={`h-3 rounded-full transition-all duration-500 ease-out ${getStageColor()}`}
            style={{ width: `${progress.percentage}%` }}
          />
        </div>
      </div>

      {/* Current Message */}
      <div className="text-center">
        <p className="text-gray-700 font-medium">{progress.message}</p>
        <p className="text-sm text-gray-500 mt-1">
          This may take a few minutes as we search multiple high-quality sources
        </p>
      </div>

      {/* Stage Indicators */}
      <div className="mt-6 grid grid-cols-5 gap-2">
        {['initializing', 'scraping', 'enriching', 'extracting', 'saving'].map((stage, index) => (
          <div key={stage} className="text-center">
            <div className={`w-3 h-3 rounded-full mx-auto mb-1 ${
              progress.stage === stage ? getStageColor() : 'bg-gray-300'
            }`} />
            <span className={`text-xs ${
              progress.stage === stage ? 'text-gray-900 font-medium' : 'text-gray-400'
            }`}>
              {stage.charAt(0).toUpperCase() + stage.slice(1)}
            </span>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ScrapingProgress;
